@import "../../global.scss";

.contact {
  background-color: white;
  display: flex;

  @include mobile {
    flex-direction: column;
  }

  .left {
    flex: 1;
    overflow: hidden;
  }
  .right {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  h2 {
    font-size: 30px;
  }
  form {
    width: 70%;
    height: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;

    input {
      width: 300px;
      height: 30px;
      font-size: 15px;

      @include mobile {
        width: 200px;
        height: 20px;
      }
    }
    textarea {
      width: 300px;
      height: 200px;
      font-size: 14px;
      @include mobile {
        width: 200px;
        height: 100px;
      }
    }
    button {
      width: 150px;
      height: 30px;
      color: whitesmoke;
      background-color: crimson;
      border: none;
      border-radius: 10px;
      font-weight: 500;
      cursor: pointer;
    }
    span {
      color: green;
    }
  }
}
